.app-info {
  background-color: $clr2;
  position: relative;
  
  padding: 160px 20px 40px 20px;
  
  @include for-normal-layout {
    padding: 0;
    padding-bottom: 40px;
    padding-top: 160px;
  }
  .content {
    .box-holder {
      padding-bottom: 0px;
      flex-direction: column;
      @include for-normal-layout {
        padding-bottom: 96px;
        flex-direction: row;
      }
      .box {
        margin-bottom: 160px;
        @include for-normal-layout {
          margin-bottom: 0px;
        }
        .logo {
          margin-bottom: 16px;
          width: 91px;
          height: 88px;
        }
        h1 {
          font-size: 20px;
          font-weight: 700;
          margin-bottom: 8px;
          text-align: center;
        }
        p {
          text-align: center;
          font-size: 14px;
          font-weight: 400;
          line-height: 21px;
          br {
            display: none;
          }
          @include for-normal-layout {
            br {
              display: block;
            }
          }
        }
      }
      .box:not(:last-child) {
        margin-right: 0px;
        @include for-normal-layout {
          margin-right: 147px;
        }
      }
    }
  }
}
