.app-hero {
  background-color: $clr1;
  // padding-bottom: 160px;
  position: relative;
  padding: 0px 20px 0px 20px;
  @include for-normal-layout {
    padding: 0;
  }
  .pattern {
    background: url(../../icons/bg-curvy-mobile.svg);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    height: 100%;
    position: absolute;
    right: 0px;
    left: 0px;

    @include for-normal-layout {
      background: url(../../icons/bg-curvy-desktop.svg);
      background-repeat: no-repeat;
      background-size: 100% 72%;
      position: absolute;
      height: 100%;
      right: 0px;
      left: 0px;
      bottom: -312px;
      

    }
  }
  .content {
    position: relative;
    .img-holder {
      margin-bottom: 36px;
      img {
        width: 304px;
        height: 225px;
        @include for-normal-layout {
          height: initial;
          width: initial;
        }
      }
    }
    h1 {
      font-size: 24px;
      font-weight: 700;
      line-height: 36px;
      text-align: center;
      margin-bottom: 19px;
      br {
        display: none;
      }
      @include for-normal-layout {
        font-size: 40px;
        line-height: 60px;
        margin-bottom: 38px;
        br {
          display: block;
        }
      }
    }
    p {
      text-align: center;
      font-size: 14px;
      font-weight: 400;
      line-height: 21px;
      margin-bottom: 33px;
      br {
        display: none;
      }
      @include for-normal-layout {
        font-size: 20px;
        line-height: 30px;
        margin-bottom: 34px;
        br {
          display: block;
        }
      }
    }
    button {
      font-size: 16px;
      font-weight: 700;
    }
  }
}
