* {
  box-sizing: border-box;
}

::selection {
  background-color: $clr1;
  color: $clr1;
}

html {
  font-family: "montserrat", sans-serif;
  line-height: 1.6;
  color: $clr2;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  background-color: #1b2330;
}

// img {
//     width: 100%;
// }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

h1 {
  font-family: "railway";
  color: $clr4;
}

p {
  margin: 0;
  font-family: "openSans";
  color: $clr4;
}

li {
  list-style: none;
}

