.app-prod {
  background-color: $clr2;
  padding: 0px 20px 180px 20px;
  @include for-normal-layout {
    padding: 0;
    padding-bottom: 180px;
  }
  .content {
    flex-direction: column;
    @include for-normal-layout {
      flex-direction: row;
    }
    .img-holder {
      margin-right: 0px;
      margin-bottom: 47px;
      @include for-normal-layout {
        margin-bottom: 0px;
        margin-right: 57px;
      }
      img {
        width: 304px;
        height: 230px;
        @include for-normal-layout {
          width: initial;
          height: initial;
        }
      }
    }
    .box {
      h1 {
        font-size: 18px;
        line-height: 24px;
        text-align: center;
        margin-bottom: 16px;

        br {
          display: none;
        }
        @include for-normal-layout {
          text-align: unset;
          font-size: 40px;
          font-weight: 700;
          line-height: 50px;
          margin-bottom: 27px;
          br {
            display: block;
          }
        }
      }
      .txt-one {
        color: #ffffff;
        font-size: 14px;
        font-family: "openSans";
        line-height: 21px;
        text-align: center;
        margin-bottom: 16px;
        br {
          display: none;
        }
        @include for-normal-layout {
          text-align: unset;
          font-family: "openSans";
          font-size: 16px;
          font-weight: 400;
          line-height: 24px;
          margin-bottom: 18px;
          br {
            display: block;
          }
        }
      }
      .txt-two {
        color: #ffffff;
        font-size: 14px;
        font-family: "openSans";
        line-height: 21px;
        text-align: center;
        margin-bottom: 16px;
        br {
          display: none;
        }
        @include for-normal-layout {
          text-align: unset;
          font-family: "openSans";
          font-size: 16px;
          font-weight: 400;
          line-height: 24px;
          margin-bottom: 26px;
          br {
            display: block;
          }
        }
      }
      .txt-three {
        font-size: 12px;
        font-family: "openSans";
        color: $clr5;
        text-align: center;
        @include for-normal-layout {
          text-align: unset;
          font-family: "openSans";
          color: $clr5;
          font-size: 16px;
          font-weight: 400;
        }
      }
    }
  }
}
