.app-creators {
  background-color: $clr2;
  padding-bottom: 330px;
  
  @include for-normal-layout {
    padding-bottom: 360px;
  }
  .content {
    flex-direction: column;
    @include for-normal-layout {
      flex-direction: row;
    }

    .box {
      position: relative;

      .pattern {
        position: absolute;
        background: url(../../img/bg-quotes.png);
        background-repeat: no-repeat;
        height: 44px;
        right: 0;
        left: 31px;
        top: -33px;
        

      }
      .box-holder {
        position: relative;

        box-shadow: 5px 5px 8px 4px rgba(56, 56, 56, 0.1);
        border-radius: 4px;
        background-color: #202a3c;
        margin-left: 0px;
        margin-bottom: 24px;
        width: 280px;
        height: 160px;
        @include for-normal-layout {
          margin-bottom: 0px;
          margin-left: 40px;
          width: 360px;
          height: 200px;
        }
        .txt-one {
          padding-top: 24px;
          padding-left: 20px;
          padding-right: 20px;
          padding-bottom: 21px;
          font-size: 10px;
          font-weight: 400;
          line-height: 18px;
          color: #ffffff;
          font-family: "openSans";
          br {
            display: none;
          }
          @include for-normal-layout {
            font-size: 14px;
            font-weight: 400;
            letter-spacing: 0.5px;
            line-height: 21px;
            padding-top: 40px;
            padding-left: 26px;
            padding-right: 0px;
            padding-bottom: 28px;
            br {
              display: block;
            }
          }
        }
        .profile {
          padding-left: 20px;

          @include for-normal-layout {
            padding-left: 26px;
          }
          .img-holder {
            padding-right: 8px;

            img {
              border-radius: 50px;
              width: 24px;
              height: 24px;
            }
          }

          .txt-two {
            color: #ffffff;
            font-family: "openSans";
            font-size: 10px;
            font-weight: 700;
            letter-spacing: 0.5px;
            line-height: 12px;
            margin-bottom: 4px;
          }
          .txt-three {
            color: #ffffff;
            font-family: "openSans";
            font-size: 7px;
            font-weight: 400;
            letter-spacing: 0.5px;
            line-height: 10px;
          }
        }
      }
    }
  }
}
