.app-early {
  .content {
    width: 336px;
    height: 353px;
    box-shadow: 5px 5px 8px rgba(0, 0, 0, 0.25);
    border-radius: 9px;
    background-color: #1b2330;
    position: absolute;
    @include for-normal-layout {
      width: 863px;
      height: 275px;
    }

    h1 {
      font-size: 18px;
      line-height: 24px;
      text-align: center;
      margin-bottom: 16px;
      @include for-normal-layout {
        text-align: unset;
        font-size: 32px;
        font-weight: 700;
        line-height: 48px;
        margin-bottom: 16px;
      }
    }
    p {
      font-size: 14px;
      font-weight: 400;
      line-height: 21px;
      margin-bottom: 39px;
      padding-left: 28px;
      padding-right: 28px;
      br {
        display: none;
      }
      @include for-normal-layout {
        padding-left: 0px;
        padding-right: 0px;
        br {
          display: block;
        }
      }
    }
    .inputn-btn-holder {
      flex-direction: column;
      @include for-normal-layout {
        flex-direction: row;
      }
      .input-holder {
        input {
          border: none;
          outline: none;
          width: 280px;
          height: 48px;
          box-shadow: 2px 3px 6px rgba(7, 4, 59, 0.16);
          border-radius: 24px;
          padding-left: 38px;
          margin-right: 0px;
          margin-bottom: 24px;
          margin-bottom: 24px;
          @include for-normal-layout {
            margin-bottom: 0px;
            margin-right: 29px;
            width: 480px;
            height: 48px;
          }

          &::placeholder {
            color: #c0c0c0;
            font-size: 12px;
            font-weight: 400;
          }
        }
      }
      button {
        font-size: 14px;
        font-weight: 700;
        width: 280px;
        height: 48px;
        @include for-normal-layout {
        }
      }
    }
  }
}
