.app-footer {
  background-color: $clr3;
  .content {
    padding-top: 250px;
    padding-bottom: 46px;
    @include for-normal-layout {
      padding-top: 200px;
      padding-bottom: 97px;
    }
    .logo {
      margin-bottom: 40px;
      background: url(../../icons/logo.svg);
      background-repeat: no-repeat;
      height: 52px;
      width: 176px;
      @include for-normal-layout {
        margin-bottom: 32px;
      }
    }

    .content-holder {
      flex-direction: column;
      @include for-normal-layout {
        flex-direction: row;
      }
      .box-one {
        margin-right: 0px;
        margin-bottom: 16px;
        @include for-normal-layout {
          margin-bottom: 0px;
          margin-right: 80px;
        }
        .location {
          padding-top: 8px;
          margin-right: 27px;
          @include for-normal-layout {
            padding-top: 4px;
          }
        }
        p {
          br {
            display: none;
          }
          @include for-normal-layout {
            br {
              display: block;
            }
          }
        }
      }

      .box-two {
        margin-right: 0px;
        margin-bottom: 80px;
        @include for-normal-layout {
          margin-right: 129px;
          margin-bottom: 0px;
        }
        .item-one {
          margin-bottom: 16px;
          .icon {
            margin-right: 23px;
          }
        }
        .item-two {
          .icon {
            margin-right: 23px;
          }
        }
      }

      .box-three {
        margin-right: 0px;
        margin-bottom: 48px;
        width: 71px;
        @include for-normal-layout {
          margin-right: 80px;
          margin-bottom: 0px;
        }

        p {
          margin-bottom: 16px;
        }
      }

      .box-four {
        width: 88px;
        margin-bottom: 80px;
        margin-right: 0px;
        @include for-normal-layout {
          margin-right: 141px;
          margin-bottom: 0px;
        }

        p {
          margin-bottom: 16px;
        }
      }

      .box-five {
        justify-content: center;
        align-items: center;
        @include for-normal-layout {
          justify-content: unset;
          align-items: unset;
        }
        .social {
          margin-right: 9px;
          @include for-normal-layout {
            margin-right: 23px;
          }
        }
      }
    }
  }
}
