.appHeader-nav-color {
  background-color: $clr1;
  .appHeader-nav {
    padding-top: 73px;
    padding-bottom: 75px;

    .logo {
      background: url(../../icons/logo-small.svg);
      height: 24px;
      width: 80px;

      @include for-normal-layout {
        background: url(../../icons/logo.svg);
        background-repeat: no-repeat;
        height: 52px;
        width: 176px;
      }
    }

    .appHeader-nav-links {
      padding: 0;
      border: none;
      margin: 0;
      @include for-normal-layout {
        margin: initial;
      }
      a {
        padding: 0px;
        text-align: unset;
        text-decoration: none;
        color: $clr4;
        font-family: "railway-regular";
        font-size: 12px;
        @include for-normal-layout {
          font-size: 16px;
        }
      }

      li {
        border: none;
      }
      li:not(:first-child) {
        margin-left: 50px;
      }
    }
  }
}
